import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownFilterProps } from './DropDownFilter.interface';
import React, { Fragment } from 'react';
import { getDistanceToBottom } from '../../../utils/getDistanceToBottom';
import { Combobox, Transition } from '@headlessui/react';
import { twMerge } from 'tailwind-merge';

const MENU_HEIGHT = 268;

const highlightQuery = (string: string, query: string) => {
  const regex = new RegExp(query, 'gi');
  return string.replace(regex, (match) => '<b>' + match + '</b>');
};

const getMenuItemClasses = (hovered: boolean, isActive: boolean) => {
  return clsx(
    'px-4 py-3 text-md zep-body-text flex w-full justify-between items-center',
    hovered ? 'bg-gray-100 text-gray-900' : 'text-zep-typography-dark-70',
    { 'bg-gray-200': isActive },
    { 'pr-12': isActive },
  );
};

export const DropDownFilter: React.FC<DropDownFilterProps> = ({
  disabled = false,
  items,
  label,
  message,
  noOptionsLabel,
  placeholder,
  readonly = false,
  state = 'default',
  ...props
}) => {
  const menuRef = React.useRef(null);
  const inputRef = React.useRef(null);
  const hasError = state === 'error';
  const iconColor = hasError ? 'text-zep-error' : state === 'info' ? 'text-zep-info' : 'text-zep-typography-dark-100';
  const [query, setQuery] = React.useState('');
  const [selectedPerson, setSelectedPerson] = React.useState(null);
  const hasInput = query !== '' || selectedPerson !== null;
  const disabledOrReadonly = disabled || readonly;

  const filteredItems =
    query === ''
      ? items
      : items.filter((item) => {
          return item.name.toLowerCase().includes(query.toLowerCase());
        });

  return (
    <div
      className={twMerge(clsx('relative', 'inline-block', 'text-left', 'w-[326px]'), props.className)}
      data-testid="zep-drop-down-filter"
      {...props}
    >
      <Combobox as="div" value={selectedPerson} onChange={setSelectedPerson} disabled={disabledOrReadonly}>
        {({ open }) => {
          const distanceToBottom = getDistanceToBottom(inputRef.current);
          const openOnTop = distanceToBottom < MENU_HEIGHT;
          const color = disabled
            ? 'text-zep-typography-dark-70/40'
            : !hasInput && !readonly
            ? 'text-zep-typography-dark-70/70'
            : 'text-zep-typography-dark-100/100';

          const _placeholder = disabled
            ? 'placeholder-zep-typography-dark-70/40'
            : !readonly
            ? 'placeholder-zep-typography-dark-70/70'
            : 'placeholder-zep-typography-dark-100';

          const inputClasses = clsx(
            'inline-flex',
            'justify-between',
            'rounded',
            'border-solid',
            'pl-4',
            'py-3',
            'zep-body-text',
            'w-full',
            'outline-none',
            'truncate',
            readonly ? 'bg-gray-100' : 'bg-white',
            hasError ? 'border-2' : 'border',
            hasInput ? 'pr-16' : 'pr-3',
            color,
            _placeholder,
            { ring: open },
            { 'border-zep-typography-dark-70': !disabled && !hasError },
            { 'border-zep-typography-dark-70/40': disabled },
            { 'border-zep-error': hasError },
            { 'active:ring': !disabledOrReadonly },
            { 'ring-offset-1': !disabledOrReadonly },
            { 'focus-visible:ring': !disabledOrReadonly },
            { 'ring-zep-focus': !disabledOrReadonly },
            { 'cursor-not-allowed': disabledOrReadonly },
          );

          const menuClasses = clsx(
            'absolute',
            'right-0',
            'z-10',
            'py-1',
            openOnTop ? 'mb-[-25px]' : 'mt-1',
            'w-[326px]',
            'max-h-[268px]',
            'origin-top-right',
            'rounded-md',
            'bg-white',
            'shadow-md',
            'ring-1,',
            'ring-black',
            'ring-opacity-5',
            'focus:outline-none',
            'overflow-hidden',
            'overflow-y-auto',
            { 'bottom-full': openOnTop },
          );

          const deleteBtnClasses = clsx('absolute', 'p-0', 'top-3', 'right-10', 'flex', 'items-center');
          const chevronClasses = clsx(
            disabledOrReadonly ? 'text-zep-typography-dark-70/40' : 'text-zep-typography-dark-70/100',
            'transition-transform',
            'duration-500',
            'shrink-0',
            {
              'rotate-180': open,
            },
          );

          return (
            <>
              {label && (
                <Combobox.Label className={clsx('block', 'mb-zep-0.5', { 'text-zep-typography-dark-70/40': disabled })}>
                  {label}
                </Combobox.Label>
              )}
              <div className="relative mt-2">
                <Combobox.Input
                  className={inputClasses}
                  autoComplete="off"
                  placeholder={placeholder}
                  ref={inputRef}
                  data-testid="zep-drop-down-filter-input"
                  onChange={(event) => setQuery(event.target.value)}
                />
                {Boolean(selectedPerson) && (
                  <button
                    className={deleteBtnClasses}
                    data-testid="zep-drop-down-filter-delete"
                    disabled={disabledOrReadonly}
                    onClick={() => {
                      setQuery('');
                      setSelectedPerson(null);
                    }}
                  >
                    <FunctionalIcon className={color} name="DeleteCircle" size={24} />
                  </button>
                )}
                <Combobox.Button className="absolute inset-y-0 right-0 flex items-center pr-2">
                  <FunctionalIcon className={chevronClasses} name="ChevronMiniDown" role="img" size={24} />
                </Combobox.Button>
                <Transition
                  as={Fragment}
                  enter="transition ease-out duration-100"
                  enterFrom="transform opacity-0 scale-95"
                  enterTo="transform opacity-100 scale-100"
                  leave="transition ease-in duration-25"
                  leaveFrom="transform opacity-100 scale-100"
                  leaveTo="transform opacity-0 scale-95"
                >
                  {filteredItems.length > 0 ? (
                    <Combobox.Options className={menuClasses} ref={menuRef} data-testid="zep-drop-down-filter-menu">
                      {filteredItems.map((item) => (
                        <Combobox.Option
                          key={item.id}
                          data-testid="zep-drop-down-filter-option"
                          value={item.name}
                          className={({ active, selected }) => getMenuItemClasses(active, selected)}
                        >
                          {({ selected }) => (
                            <>
                              <span
                                className={clsx('truncate')}
                                dangerouslySetInnerHTML={{ __html: highlightQuery(item.name, query) }}
                              />
                              {selected && (
                                <FunctionalIcon
                                  name="Check"
                                  role="img"
                                  size={24}
                                  className="text-zep-green-50 absolute right-4"
                                />
                              )}
                            </>
                          )}
                        </Combobox.Option>
                      ))}
                    </Combobox.Options>
                  ) : (
                    <Combobox.Options className={menuClasses} ref={menuRef}>
                      <Combobox.Option
                        data-testid="zep-drop-down-filter-no-options"
                        value={0}
                        className={clsx('block py-3 text-md zep-body-text text-center')}
                      >
                        {noOptionsLabel}
                      </Combobox.Option>
                    </Combobox.Options>
                  )}
                </Transition>
              </div>
            </>
          );
        }}
      </Combobox>
      {message && (
        <div className={clsx('mt-[6px]', 'zep-tagline', 'flex', iconColor)}>
          {state !== 'default' && (
            <FunctionalIcon
              name={state === 'error' ? 'Warning' : 'Info'}
              role="img"
              size={24}
              className="mr-2 shrink-0"
            />
          )}
          <span>{message}</span>
        </div>
      )}
    </div>
  );
};
